import { useState } from 'react'
import Build from 'component/Build'
import Maintenance from 'component/Maintenance'
import Proposal from 'component/Proposal'
import ViewPop from 'component/ViewPop'
import { useSelector } from 'react-redux';

function App() {
  const [tabN, tabEvent] = useState(0)
  const clickTab = (idx) => {
    tabEvent(idx)
  }
  const isPop = useSelector((state) => state.popState.isPop )
  
  return (
    <>
      <p className="title">Portfolio</p>
      <div className="tabs">
        <button className={(tabN === 0 && "on")} onClick={() => clickTab(0)}></button>
        <button className={(tabN === 1 && "on")} onClick={() => clickTab(1)}></button>
        <button className={(tabN === 2 && "on")} onClick={() => clickTab(2)}></button>
      </div>
      {
        {
          0 : <Build />,
          1 : <Maintenance />,
          2 : <Proposal />
        }[tabN]
      }
      {
        isPop && <ViewPop />
      }
    </>
  );
}

export default App;
